<template>
    <a-layout style="height: 100vh;">
  
      <!-- 左侧菜单 -->
      <!-- :resize-directions="['right']" -->
      <a-layout-sider
        collapsible
        style="
        max-width: 95%;
        width: auto;
      ">
  
        <a-menu @menu-item-click="onClickMenuItem" :selected-keys="this.$route.fullPath.split('?').at(0)" >
          <a-menu-item key="/teacher/home"><template #icon><icon-home /></template>主页</a-menu-item>

          <a-menu-item key="/teacher/apply_for_course"><template #icon><icon-apps /></template>申请开课</a-menu-item>
          <a-menu-item key="/teacher/giveScoreForm"><template #icon><icon-pen-fill /></template>课程打分</a-menu-item>
          <a-menu-item key="/teacher/courses"><template #icon><icon-calendar-clock /></template>我的课程</a-menu-item>
          <a-menu-item key="/teacher/applications"><template #icon><icon-storage /></template>我的申请</a-menu-item>
  
        </a-menu>
        
      </a-layout-sider>
  
      <a-layout-content>
        <router-view></router-view>
      </a-layout-content>
  
    </a-layout>
  </template>
  
  <script>
  import auth_api from "@/api/auth-api"

  export default {
    
    data() {
      return {
        user_id: "",
      }
    },
    mounted() {
      this.user_id = this.$route.query.user_id
      sessionStorage.setItem("user_id",this.user_id)
      auth_api.setcookie("user_id",this.user_id,1)
    },
    methods: {
      // get_current_page(){
      //    this.$route.fullPath.split('?').at(0)
      //   return []
      // },
      onClickMenuItem(key) {
        switch(key) {
          case "/teacher/home": {
            this.$router.push({ path: "/teacher/home" })
            break
          }
          case "/teacher/apply_for_course": {
            this.$router.push({ path: "/teacher/apply_for_course" })
            break
          }
          case "/teacher/giveScoreForm": {
            this.$router.push({ path: "/teacher/giveScoreForm" })
            break
          }
          case "/teacher/courses": {
            this.$router.push({ path: "/teacher/courses" })
            break
          }
          case "/teacher/applications": {
            this.$router.push({ path: "/teacher/applications" })
            break
          }
          default: {
            this.$message.info(key) /* tmp */
          }
        }
      }
    }
  }
  </script>
  
  <style scoped>
  
  </style>